<template>
  <transition name="modal-fade">
    <div v-if="isOpen" class="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black bg-opacity-40 backdrop-blur-sm" @click.self="close">
      <div class="w-full max-w-4xl max-h-[90vh] overflow-hidden macos-card modal-shadow animate-modal-slide">
        <!-- Header -->
        <div class="sticky top-0 bg-white border-b border-gray-100 px-6 py-4 flex items-center justify-between z-10">
          <div class="flex items-center space-x-3">
            <div class="w-10 h-10 rounded-lg bg-gradient-to-br from-blue-500 to-blue-600 flex items-center justify-center">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
              </svg>
            </div>
            <div>
              <h2 class="text-xl font-bold text-gray-800">{{ project.name }}</h2>
              <p class="text-sm text-gray-500"></p>
            </div>
          </div>
          <button @click="close" class="w-8 h-8 rounded-lg hover:bg-gray-100 flex items-center justify-center transition-colors">
            <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 text-gray-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>

        <!-- Content - Scrollable -->
        <div class="overflow-y-auto max-h-[calc(90vh-80px)] px-6 py-6 space-y-6">
          
          <!-- Status & Priority Bar -->
          <div class="flex items-center gap-3">
            <span :class="{
              'status-pending': project.status === '',
              'status-active': project.status === '',
              'status-completed': project.status === '',
              'status-delayed': project.status === ''
            }">
              {{ project.status || '' }}
            </span>
            <span :class="{
              'bg-red-50 text-red-600': project.priority === '',
              'bg-yellow-50 text-yellow-600': project.priority === '',
              'bg-green-50 text-green-600': project.priority === ''
            }" class="px-2 py-1 rounded-md text-xs font-medium">
              {{ project.priority || '' }}
            </span>
            <div class="flex-1"></div>
            <div class="text-sm text-gray-500">
              <span class="font-bold text-blue-600">{{ project.progress || 0 }}%</span>
            </div>
          </div>

          <!-- Progress Bar -->
          <div class="w-full bg-gray-100 rounded-full h-2">
            <div class="bg-gradient-to-r from-blue-500 to-blue-600 h-2 rounded-full transition-all duration-500" 
                 :style="{width: (project.progress || 0) + '%'}"></div>
          </div>

          <!-- Section 1:  -->
          <section class="bg-gray-50 rounded-xl p-5 space-y-4">
            <h3 class="text-lg font-bold text-gray-800 flex items-center">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 text-blue-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
              
            </h3>
            <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div v-if="project.proposalUnit">
                <label class="text-xs text-gray-500 font-medium"></label>
                <p class="text-sm text-gray-800 mt-1">{{ project.proposalUnit }}</p>
              </div>
              <div v-if="project.proposalContact">
                <label class="text-xs text-gray-500 font-medium"></label>
                <p class="text-sm text-gray-800 mt-1">{{ project.proposalContact }}</p>
              </div>
              <div v-if="project.department">
                <label class="text-xs text-gray-500 font-medium"></label>
                <p class="text-sm text-gray-800 mt-1">{{ project.department }}</p>
              </div>
              <div v-if="project.owner">
                <label class="text-xs text-gray-500 font-medium"></label>
                <p class="text-sm text-gray-800 mt-1">{{ project.owner }}</p>
              </div>
              <div v-if="project.startTime">
                <label class="text-xs text-gray-500 font-medium"></label>
                <p class="text-sm text-gray-800 mt-1">{{ project.startTime }}</p>
              </div>
              <div v-if="project.dueDate">
                <label class="text-xs text-gray-500 font-medium"></label>
                <p class="text-sm text-gray-800 mt-1">{{ project.dueDate }}</p>
              </div>
            </div>
            <div v-if="project.relatedUnits && project.relatedUnits.length > 0">
              <label class="text-xs text-gray-500 font-medium"></label>
              <div class="flex flex-wrap gap-2 mt-2">
                <span v-for="(unit, index) in project.relatedUnits" :key="index" 
                      class="px-2 py-1 bg-blue-50 text-blue-600 rounded text-xs">
                  {{ unit }}
                </span>
              </div>
            </div>
          </section>

          <!-- Section 2:  -->
          <section class="bg-gray-50 rounded-xl p-5 space-y-4">
            <h3 class="text-lg font-bold text-gray-800 flex items-center">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 text-green-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
              </svg>
              
            </h3>
            <div v-if="project.projectReason" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.projectReason }}</p>
            </div>
            <div v-if="project.currentSituation" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.currentSituation }}</p>
            </div>
            <div v-if="project.difficulties" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.difficulties }}</p>
            </div>
            <div v-if="project.crossUnitIssues" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.crossUnitIssues }}</p>
            </div>
            <div v-if="project.attemptedSolutions" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.attemptedSolutions }}</p>
            </div>
          </section>

          <!-- Section 3:  -->
          <section class="bg-gray-50 rounded-xl p-5 space-y-4">
            <h3 class="text-lg font-bold text-gray-800 flex items-center">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 text-purple-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 19v-6a2 2 0 00-2-2H5a2 2 0 00-2 2v6a2 2 0 002 2h2a2 2 0 002-2zm0 0V9a2 2 0 012-2h2a2 2 0 012 2v10m-6 0a2 2 0 002 2h2a2 2 0 00 2-2m0 0V5a2 2 0 012-2h2a2 2 0 012 2v14a2 2 0 01-2 2h-2a2 2 0 01-2-2z" />
              </svg>
              
            </h3>
            <div v-if="project.projectGoals" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.projectGoals }}</p>
            </div>
            <div v-if="project.expectedFeatures" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.expectedFeatures }}</p>
            </div>
            <div v-if="project.targetUsers" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.targetUsers }}</p>
            </div>
            <div v-if="project.expectedBenefits" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.expectedBenefits }}</p>
            </div>
            <div v-if="project.estimatedTimeline" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed">{{ project.estimatedTimeline }}</p>
            </div>
          </section>

          <!-- Section 4:  -->
          <section class="bg-gray-50 rounded-xl p-5 space-y-4">
            <h3 class="text-lg font-bold text-gray-800 flex items-center">
              <svg xmlns="http://www.w3.org/2000/svg" class="h-5 w-5 mr-2 text-orange-500" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
              </svg>
              
            </h3>
            <div v-if="project.successMetrics" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.successMetrics }}</p>
            </div>
            <div v-if="project.requiredResources" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.requiredResources }}</p>
            </div>
            <div v-if="project.riskAssessment" class="space-y-2">
              <label class="text-xs text-gray-500 font-medium"></label>
              <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.riskAssessment }}</p>
            </div>
          </section>

          <!-- Legacy Description (if exists and not covered above) -->
          <section v-if="project.description" class="bg-gray-50 rounded-xl p-5 space-y-2">
            <h3 class="text-lg font-bold text-gray-800"></h3>
            <p class="text-sm text-gray-700 leading-relaxed whitespace-pre-wrap">{{ project.description }}</p>
          </section>

        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'ProjectDetailModal',
  props: {
    project: {
      type: Object,
      required: true
    },
    isOpen: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    close() {
      this.$emit('close');
    }
  },
  watch: {
    isOpen(newVal) {
      if (newVal) {
        document.body.style.overflow = 'hidden';
      } else {
        document.body.style.overflow = '';
      }
    }
  },
  beforeUnmount() {
    document.body.style.overflow = '';
  }
}
</script>

<style scoped>
.modal-fade-enter-active, .modal-fade-leave-active {
  transition: opacity 0.2s ease;
}
.modal-fade-enter-from, .modal-fade-leave-to {
  opacity: 0;
}

@keyframes modal-slide {
  from {
    transform: scale(0.95);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}

.animate-modal-slide {
  animation: modal-slide 0.2s ease;
}
</style>
